import React, { createContext, useContext, useState } from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

// core components
import Admin from "layouts/Admin.js";
import Login from 'views/Login'

import "assets/css/material-dashboard-react.css?v=1.10.0";


export const LoginContext = createContext({
  user: '',
  setUser: ()=>{},
})

export const ToastContext = createContext({toastData: {state: false,type: 'success',message: 'this is a test message'},
  setToastData:()=>{}})

function PrivateRoute({children, ...rest }) {
  
  const auth = useContext(LoginContext);
  console.log(auth);
  return (

    <Route
      {...rest}
      render={({ location }) =>
        auth.user ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: location }
            }}
          />
        )

      }
    />
  );
}

function ProvideAuth({ children }) {
  const [toastData, setToastData] = React.useState({
    state: false,
    message: 'Test Message',
    type: 'success',
    vertical: 'top',
    horizontal: 'right'
  })
  const [user, setUser] = useState(localStorage.getItem('user') || '');
  const [userData, setUserData] = useState({});
 console.log(user);
  return (
    <ToastContext.Provider value={{toastData, setToastData}}> 
    <LoginContext.Provider value={{user,setUser,userData, setUserData}}>
      {children}
    </LoginContext.Provider>
    </ToastContext.Provider>

  );
}


ReactDOM.render(
  <ProvideAuth>
  <BrowserRouter basename="/client">
    <Switch>
      <PrivateRoute path='/admin' >
         <Admin/>
      </PrivateRoute>  
      {/* <Route path="/admin" component={Admin} /> */}
      <Route path="/login" component={Login} />
      <Route path='/' >
        <Redirect to='/admin' />
      </Route>
    </Switch>
  </BrowserRouter>
  </ProvideAuth>
  ,
  document.getElementById("root")
);
