import React from "react";
import classNames from "classnames";
import PropTypes from "prop-types";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Hidden from "@material-ui/core/Hidden";
// @material-ui/icons
import Menu from "@material-ui/icons/Menu";
// core components
import AdminNavbarLinks from "./AdminNavbarLinks.js";
import RTLNavbarLinks from "./RTLNavbarLinks.js";
import Button from "components/CustomButtons/Button.js";
import Snackbar from "../SnackBar";

//hooks
import { useRouteName } from "hooks";

import styles from "assets/jss/material-dashboard-react/components/headerStyle.js";
import { useLocation } from "react-router";
import { findDOMNode } from "react-dom";
import {Helmet} from "react-helmet";

const useStyles = makeStyles(styles);

export default function Header(props) {
  const location = useLocation();
  console.log("location",location);
  const classes = useStyles();
  const routeName = useRouteName();
  const { color, routes } = props;
  const appBarClasses = classNames({
    [" " + classes[color]]: color,
  });
  
 
  let current = routes.find(ele=>(ele.path === (location.pathname).substring(6)))?.name;
 
  console.log("path",current)
  
  
  return (
    <AppBar className={classes.appBar + appBarClasses}>
      <Helmet>
                <meta charSet="utf-8" />
                <title>{(current === 'Dashboard' ? 'Employee Management System | Infonic Solutions' : current) ?? "Employee Management System | Infonic Solutions"}</title>
            </Helmet>

      <Toolbar className={classes.container}>
        <div className={classes.flex}>
          {/* Here we create navbar brand, based on route name */}
        <Snackbar />
          <Button color="transparent" href="#" className={classes.title}>
            {current ?? 'Employee Management System'}
          </Button>
        </div>
        <Hidden smDown implementation="css">
          {props.rtlActive ? <RTLNavbarLinks /> : <AdminNavbarLinks />}
        </Hidden>
        <Hidden mdUp implementation="css">
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={props.handleDrawerToggle}
          >
            <Menu />
          </IconButton>
        </Hidden>
      </Toolbar>
    </AppBar>
  );
}

Header.propTypes = {
  color: PropTypes.oneOf(["primary", "info", "success", "warning", "danger"]),
  rtlActive: PropTypes.bool,
  handleDrawerToggle: PropTypes.func,
  routes: PropTypes.arrayOf(PropTypes.object),
};
