import React, { useEffect } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { useFormik } from 'formik';

import InputLabel from "@material-ui/core/InputLabel";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import TreeList from 'components/TreeList'
import ChipList from 'components/ChipList'
import { HOST } from '../config.js';
import { positions } from "@mui/system";
import { ToastContext } from "index";



const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};

const useStyles = makeStyles(styles);

export default function UserProfile() {
  const classes = useStyles();
  const { setToastData } = React.useContext(ToastContext);
  const [listData, setListData] = React.useState([]);
  const [ positionList, setPositionList] = React.useState([]);
  const [position, setPosition ]  = React.useState('');
  const [refresh, setRefresh] = React.useState(false);
  const [currentId, setCurrnetId] = React.useState(false);


  const sendRequest =(value)=>{
    console.log(value)
    fetch(`${HOST}/api/department`,{
      method: 'POST',
      body: JSON.stringify(value),
      headers:{
        'Content-Type':'application/json'
      }
    }).then(res=>res.json())
    .then(res=>{
      if(!res.ok){
        setToastData({state: true, message: res.message, type: 'success'})
      }else{
        setToastData({state: true, message: res.message, type: 'error'})
      }
      console.log(res)
      handleReset();
      setPositionList([]);
      setPosition('');
      setRefresh(!refresh);
    })
  }
  const sendUpdataRequest =(value)=>{
    console.log(value)
    fetch(`${HOST}/api/department`,{
      method: 'PUT',
      body: JSON.stringify(value),
      headers:{
        'Content-Type':'application/json'
      }
    }).then(res=>res.json())
    .then(res=>{
      if(!res.ok){
        setToastData({state: true, message: res.message, type: 'success'})
      }else{
        setToastData({state: true, message: res.message, type: 'error'})
      }
      console.log(res)
      handleReset();
      setPositionList([]);
      setPosition('');
      setCurrnetId('');
      setRefresh(!refresh);

    })
  }

  const sendDeleteRequest =(value)=>{
    fetch(`${HOST}/api/department/${value}`,{
      method: 'DELETE',
      headers:{
        'Content-Type':'application/json'
      }
    }).then(res=>res.json())
    .then(res=>{
      if(!res.ok){
        setToastData({state: true, message: res.message, type: 'success'})
      }else{
        setToastData({state: true, message: res.message, type: 'error'})
      }
      console.log(res)
      handleReset();
      setPositionList([]);
      setPosition('');
      setCurrnetId('');
      setRefresh(!refresh);

    })
  }


useEffect(()=>{
  fetch(`${HOST}/api/department`)
  .then(res=>res.json())
  .then(res=>setListData(res))

  return ()=>{
    setListData([]);
  }
},[refresh]);

 const initValue = {
    department: '',
    list:[],
  }

  const { handleChange, handleSubmit, handleBlur,handleReset, values, setValues} = useFormik({
    initialValues: initValue,
    // validationSchema:'',
    onSubmit: values => {
      if(currentId.length){
        sendUpdataRequest({_id: currentId,department: values.department, list: positionList});   
      }
     sendRequest({department: values.department, list: positionList});
    },
  });

  const AddPositionHandler = ()=>{
    if(position !== '' && !positionList.includes(position)){
      setPositionList((pre)=>[...pre,position]);
      setPosition('');
    }
     
  }
 

  const handleDelete = (chipToDelete) => {
    setPositionList((chips) => chips.filter((chip) => chip !== chipToDelete));
  };

  const handleChangePosition = (e)=>{
   const val = e.target.value;
   setPosition(val);
  }
  const handleChangeTree =(val)=>{
   
    const find = listData.find(ele=>ele._id === val);
    setValues({
      department: find.department,
    })
    setPositionList(find.list)
    setCurrnetId(find._id);
  }

  const AddNewHandler =()=>{
    setPositionList([]);
    setValues({
      department:'',
    })
    setCurrnetId('');
  }
  const DeltePositionHandler = () => {
    sendDeleteRequest(currentId);

  }
  return (
    <div>
      <GridContainer>
        <GridItem xs={12} sm={12} md={8}>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>Add Department</h4>
              <p className={classes.cardCategoryWhite}>Complete profile</p>
            </CardHeader>
            <CardBody>
              <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                  <CustomInput
                    labelText="Department Name"
                    id="departmentName"
                    inputProps={{
                      name:'department',
                      onChange:handleChange,
                      onBlur:handleBlur,
                      value:values.department
                    }}
                   
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                </GridContainer>
                <GridContainer>
                  <GridItem xs={12} sm={12} md={12}>
                  {positionList.length ? <ChipList data={positionList} handleDelete={handleDelete} /> : ''}
                   </GridItem>
                  </GridContainer>  
                <GridContainer>
                
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="position"
                    id="position"
                    inputProps={{
                      name:"position",
                      onChange:handleChangePosition,
                      value:position
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                  <Button color="primary" onClick={AddPositionHandler} >Add Position</Button>
                  </GridItem>
                  <GridItem>
                </GridItem>
              </GridContainer> 
              
            </CardBody>
            <CardFooter>
              <Button onClick={handleSubmit} color="primary">{currentId.length ? "Update" : "Insert "} Department</Button>
              {currentId !='' && currentId.length && 
                  <Button color="primary" onClick={DeltePositionHandler} >Delete Department</Button>
                 } 
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={12} md={4}>
          <Card profile>
           
            <CardBody  profile>
              <Button color="primary" round onClick={AddNewHandler}>
                Add New
              </Button>
              <TreeList data={listData} handleChangeTree={handleChangeTree} />
             
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
}
