import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Checkbox from '@mui/material/Checkbox';
import Avatar from '@mui/material/Avatar';
import SearchIcon from '@mui/icons-material/Search';
import InputBase from '@mui/material/InputBase';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';


export default function ChatSidebar({messagesUser, listClickHandler}) {
  const [checked, setChecked] = React.useState([1]);

  const handleToggle = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
  };

  return (
    <List dense sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
      <ListItemButton sx={{padding: 1}}>
           <ListItemAvatar>
                <Avatar
                  alt={`Avatar `}
                  src={`/static/images/avatar/1.jpg`}
                />
          </ListItemAvatar>
        <ListItemText id={1} primary={`Line item `} />
      </ListItemButton>
      
      <Paper
      sx={{ p: '2px 4px', display: 'flex', alignItems: 'center' ,borderRadius: 1}}
    >
      <InputBase
        sx={{ ml: 1, flex: 1 }}
        placeholder="Search.."
        inputProps={{ 'aria-label': 'search google maps' }}
      />
      <IconButton sx={{ p: '10px' }} aria-label="search">
        <SearchIcon />
      </IconButton>
    </Paper>
      {messagesUser.map((value) => {
        const labelId = `checkbox-list-secondary-label-${value._id}`;
        return (
          <ListItem
            key={value}
            disablePadding
            onClick={()=>listClickHandler(value._id)}
          >
            <ListItemButton>
              <ListItemAvatar>
                <Avatar
                  alt={`Avatar n${value._id + 1}`}
                  src={`/static/images/avatar/${value._id + 1}.jpg`}
                />
              </ListItemAvatar>
              <ListItemText id={labelId} primary={`${value.name}`} />
            </ListItemButton>
          </ListItem>
        );
      })}
    </List>
  );
}