import React, { useState, useEffect, useContext, useCallback } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { useFormik } from 'formik';

import MUIDataTable from "mui-datatables";
import TablePagination from '@mui/material/TablePagination';
import moment from 'moment';

// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import DropDownMenu from 'components/DropDownMenu';
import Button from "components/CustomButtons/Button.js";

import { CustumContex } from "layouts/Admin";
import { Route, useHistory, useLocation, Switch } from "react-router";
import { HOST } from '../../config.js'
import { ToastContext } from "index";
import { Box } from "@mui/system";
import { Typography } from "@material-ui/core";
import Tab from '@mui/material/Tab';
import Tabs from '@mui/material/Tabs';



const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF",
    },
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
};

const useStyles = makeStyles(styles);





export default function TableList() {
  const { setToastData } = React.useContext(ToastContext);
  const {pathname} = useLocation();

  const history = useHistory();
  const [ dataList, setDataList ] = useState([]);
  const [ refresh, setRefresh ] = useState(false);
  const {currentId, setCurrentId} = useContext(CustumContex);


  const [page, setPage] = useState(0)
  const [rowPerPage, setRowsPerPage] = useState(10);
  const [rowsTotal, setRowsTotal ] = useState(10);
  const [selected, setSelected] = useState([]);
  
  


  const [ statusToggleUrl, setStatusToggleUrl ] = useState({
    url: `${HOST}/api/employ?page=${page}&row=${rowPerPage}`, name: 'All Employee List',
    ...(pathname === "/admin/employees/active" ? {url:`${HOST}/api/employee/status/active?page=${page}&row=${rowPerPage}`, name: 'Active Employee List'}
    : pathname === '/admin/employees/former' ? {url:`${HOST}/api/employee/status/former?page=${page}&row=${rowPerPage}`, name: 'Former Employee List'}
    : {url:`${HOST}/api/employ?page=${page}&row=${rowPerPage}`, name: 'All Employee List'})
  });

  const [tabValue, setTabValue] = React.useState('All');

  const [mounted, setMounted] = useState(false);


  console.log("path", pathname)


 
  useEffect(() => {
    setMounted(true)
    fetch(statusToggleUrl.url)
    .then(res=> res.json())
    .then(res=>{
      (res.data).map(data=>setDataList(doc=>[...doc, [
      data._id,
      data.name,
      data.father,
      data.email,
      moment(data.dob).format('MMM DD, YYYY'),
      data.mobileNo,
      data.address,
      data.status,
      data.position,
      data.department,
      moment(data.joinDate).format('MMM DD, YYYY'),
      data.salary,
      ...(data.formerDate ? [moment(data.formerDate).format('MMM DD, YYYY')] : ['Current Employee'])
     
    ]]));
    setRowsTotal(res.total)
  })
    return () => {
      setDataList([]);
    }
  }, [refresh, statusToggleUrl]);


  const deleteRequest=(value)=>{
    fetch(`${HOST}/api/employ?page`,{
      method:"DELETE",
      body:JSON.stringify({ids: value}),
      headers:{
        'Content-Type':"application/json"
      }
    }).then(res=>{
      return res.json();
    })
    .then(res=>{
      if(!res.ok){
        setToastData({state: true, message: res.message, type: 'success'})
      }else{
        setToastData({state: true, message: res.message, type: 'error'})
      }
      setRefresh(!refresh)
      console.log(res)
    })
  }


  const actionHander = (value,typ) => {
    if(typ === 'Delete'){
      deleteRequest([value]);
    }else if (typ === 'Edit'){
      setCurrentId(value);
      history.push(`user/${value}`);
    }else if (typ === 'View'){
    history.push(`view/${value}`);
  }
    console.log(value,typ);
  }


  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setRefresh(!refresh);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
    setRefresh(!refresh);
  };

  const deleteHander = (value) => {
    const val = value.data
    const vmap = val.map(ele=>ele.index);
    const select = dataList.filter((ele,i)=>vmap.includes(i));
    console.log(select)
    const smap = select.map((ele)=>{
      return ele[0]
    });
    console.log(smap)
    deleteRequest(smap)

  }
 const handleChangeTab = (event, value) => {
    const val = value
    setTabValue(val);
    switch (val) {
      case 'Active':
        history.push('/admin/employees/active');
        
        setStatusToggleUrl({url:`${HOST}/api/employee/status/active?page=${page}&row=${rowPerPage}`, name: 'Active Employee List'})
        setPage(0);
        setRowsPerPage(10);
        break;
      case 'Former':
        history.push('/admin/employees/former');
        
        setStatusToggleUrl({url:`${HOST}/api/employee/status/former?page=${page}&row=${rowPerPage}`,name: 'Former Employee List'})
        setPage(0);
        setRowsPerPage(10);
        break; 
      default:
        history.push('/admin/employees/all');
        setStatusToggleUrl({url:`${HOST}/api/employ?page=${page}&row=${rowPerPage}`,name: 'All Employee List'})
        setPage(0);
        setRowsPerPage(10)
        break;
    }
  }



  const columns = [
    {
      name: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          const actionHanderTemp = (type)=>{
            const typ = type.target.textContent;
            actionHander(value,typ);
          }

          return (
            <>
              <DropDownMenu actionHander={actionHanderTemp} />
            </>
          );
        }
      }
    },
    "Name",
    "father",
    "email",
     "DOB",
      "M.No", "Address","status","Position","Department","joining date", "salary","former"];


const options = {
  filterType: 'checkbox',
  pagination: false,
  onRowsDelete: (value)=>{deleteHander(value)},
  setRowProps: row => {
    const color = row[7] === "active" ? '#d9ffd5' : ' #ffa9a9 ';
      return {
        style: { background: color }
      };
  }
};
  const classes = useStyles();
  return (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
       
        <Card>
          <CardHeader color="primary">
          
            <h4 className={classes.cardTitleWhite}>Employees Table</h4>
            <p className={classes.cardCategoryWhite}>
              Here is Detail for All Employees
            </p>
          </CardHeader>
          <CardBody>
          <MUIDataTable 
             title={
               [
               <Typography variant="h6">{statusToggleUrl.name}</Typography>,
               <Box sx={{ width: '100%' }}>
               <Tabs
                 value={tabValue}
                 onChange={handleChangeTab}
                 textColor="secondary"
                 indicatorColor="secondary"
                 aria-label="secondary tabs example"
               >
                 <Tab value="All" label="All" />
                 <Tab value="Active" label="Active" />
                 <Tab value="Former" label="Former" />
               </Tabs>
             </Box>
               ]} 
             data={dataList} 
             columns={columns} 
             options={options} 
           />
             <TablePagination
              rowsPerPageOptions={[5, 10, 25, { label: '100', value: 100 }]}
              colSpan={3}
              count={+rowsTotal}
              rowsPerPage={rowPerPage}
              page={page}
              SelectProps={{
                inputProps: {
                  'aria-label': 'rows per page',
                },
                native: true,
              }}
              onPageChange={handleChangePage}
              onRowsPerPageChange={handleChangeRowsPerPage}
            />
          </CardBody>
        </Card>
      </GridItem>
      
    </GridContainer>
  );
}
