import React, { useEffect, useState, useContext } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { useFormik } from 'formik';
import Moment from 'react-moment';
import moment from 'moment'
import * as Yup from 'yup';



import InputLabel from "@material-ui/core/InputLabel";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import DatePicker from 'components/DatePicker'
import DilogeBox from 'components/DilogeBox'
import PdfDocument from 'components/PdfDocument'
import NormalSelect from 'components/NormalSelect'
import { useFileUpload } from 'use-file-upload'
import Avatar from '@mui/material/Avatar';
import Box from '@mui/material/Box'

import CustomTable from "components/CustumTabal";

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';


import dummyImg from "assets/img/dummy/male_user.webp";
import { string } from "prop-types";
import { HOST } from '../../config.js'
import { title } from "assets/jss/material-dashboard-react.js";
import { useParams } from "react-router";
import { useHistory } from "react-router";
import { Link } from "react-router-dom";


const styles = {
    cardCategoryWhite: {
        color: "rgba(255,255,255,.62)",
        margin: "0",
        fontSize: "14px",
        marginTop: "0",
        marginBottom: "0",
    },
    cardTitleWhite: {
        color: "#FFFFFF",
        marginTop: "0px",
        minHeight: "auto",
        fontWeight: "300",
        fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
        marginBottom: "3px",
        textDecoration: "none",
    },
};

const useStyles = makeStyles(styles);

export default function ViewProfile() {
    const { id } = useParams();
    const history = useHistory();

    const [departmentList, setDepartmentList] = React.useState([])
    const [positionList, setPositionList] = React.useState([])
    const [data, setData] = useState({});
    const [isEdit, setIsEdit] = useState(false)
    const [re, setRe] = useState(false);
    const [hr, setHr] = useState({ name: '', title: '' });
    const [manager, setManager] = useState({ name: '', title: '' })
    const [employList, setEmployList] = useState([]);
    const classes = useStyles();
    const [files, selectFiles] = useFileUpload();




    useEffect(() => {
        fetch(`${HOST}/api/employ/${id}`)
            .then(res => res.json())
            .then(res => {
                setData(res)
                fetch(`${HOST}/api/employpos?department=${encodeURIComponent(res.department)}&position=Manager`)
                    .then(rese => rese.json())
                    .then(rese => setManager(rese));
            })
        return () => {
            setData({});
        }
    }, [re]);

    React.useEffect(() => {
        fetch(`${HOST}/api/department`)
            .then(res => res.json())
            .then(res => {
                console.log(res)
                setDepartmentList(res);
            })
        return () => {
            setDepartmentList([]);
        }
    }, []);





    React.useEffect(() => {
        fetch(`${HOST}/api/employpos?department=Management&position=Human Resources (HR)`)
            .then(res => res.json())
            .then(res => setHr(res));
    }, []);
    return (
        <div>
            <GridContainer>
                <GridItem xs={12} sm={12} md={8}>
                    <Card>
                        <CardHeader color="primary">
                            <h4 className={classes.cardTitleWhite}>View Profile</h4>
                            <p className={classes.cardCategoryWhite}>Complete profile</p>
                        </CardHeader>
                        <CardBody>
                            <GridContainer>
                                <GridItem xs={12} sm={12} md={12}>
                                    <CustomTable data={data} />                                   
                                </GridItem>
                            </GridContainer>
                        </CardBody>
                    </Card>
                </GridItem>

                <GridItem xs={12} sm={12} md={4}>
                    <Card>
                        <Box sx={{
          display: 'flex',
          flexDirection: 'column',
          width: 150,
          margin: 'auto',
          paddingBottom: 1,
        }} >
                            
                        <CardAvatar profile>
                            <a href="#pablo" onClick={(e) => e.preventDefault()}>
                                <img src={data.image || dummyImg} alt={data.name} />
                            </a>
                        </CardAvatar>
                        <DilogeBox
                            children={<PdfDocument type='experience' data={data} hr={hr} manager={manager} />}
                            label='Expireance letter'
                            color="primary"
                            button='Expireance Letter' />
                        <DilogeBox
                            children={<PdfDocument type='Recommendation' data={data} hr={hr} manager={manager} />}
                            employ={employList}
                            label='Recommendation'
                            button='Recommendation'
                            />
                        <DilogeBox
                            children={<PdfDocument data={data} hr={hr} manager={manager} />}
                            label='Offer letter'
                            color="primary"
                            button='Offer Letter' />
                         <Button color='warning' round onClick={()=>history.push(`/admin/user/${id}`)}>
                             Update
                        </Button>   
                      </Box>
                    </Card>
                </GridItem>
            </GridContainer>

        </div>
    );
}
