import * as React from 'react';
import Button from '@mui/material/Button';
import Snackbar from '@mui/material/Snackbar';
import { ToastContext } from 'index';






import MuiAlert from '@mui/material/Alert';

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

export default function PositionedSnackbar() {
    const {toastData, setToastData} = React.useContext(ToastContext);

  const handleClose = () => {
    setToastData({...toastData, state: false});
  };

  return (
    <div>
      <Snackbar
        anchorOrigin={{ vertical: toastData.vertical??'top', horizontal: toastData.horizontal??'right' }}
        open={toastData.state}
        onClose={handleClose}
        autoHideDuration={6000}
      >
       <Alert onClose={handleClose} severity={toastData.type??'success'} sx={{ width: '100%' }}>
          {toastData.message}
        </Alert>
      </Snackbar>
    </div>
  );
}