import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { useFormik } from 'formik';
import * as Yup from 'yup';

import InputLabel from "@material-ui/core/InputLabel";
import Box from '@mui/material/Box';
import { useFileUpload } from 'use-file-upload'
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import NormalSelect from 'components/NormalSelect'
import DatePicker from 'components/DatePicker'
import Avatar from '@mui/material/Avatar';


import avatar from "assets/img/faces/marc.jpg";
import useForm from "components/validation/useForm";
import Validator from "components/validation/Validator";
import { HOST } from '../../config.js'
import { ToastContext } from "index";


const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};

const useStyles = makeStyles(styles);

export default function UserProfile() {
  const { setToastData } = React.useContext(ToastContext);
  const classes = useStyles();
  const [departmentList, setDepartmentList] = React.useState([])
  const [positionList, setPositionList] = React.useState([])

  const [files, selectFiles] = useFileUpload();


  const sendRequest = (value) => {
    console.log(value)
    fetch(`${HOST}/api/employ`, {
      method: 'POST',
      body: value
    }).then(res =>(res.json()))
      .then(res => {
        handleReset();
        if(!res.ok){
          setToastData({state: true, message: res.message, type: 'success'})
        }else{
          setToastData({state: true, message: res.message, type: 'error'})
        }
        console.log(res)
      })

  }
  React.useEffect(() => {
    fetch(`${HOST}/api/department`)
      .then(res => res.json())
      .then(res => (setDepartmentList(res), console.log(res)))
    return () => {
      setDepartmentList([]);
    }
  }, []);
  const validationSchema = Yup.object().shape({
    title: Yup.string().required(),
    name: Yup.string().min(2, 'Too Short!').max(50, 'Too Long!').required('Required'),
    father: Yup.string().min(2, 'Too Short!').max(50, 'Too Long!').required('Father Name is Required'),
    mobileNo: Yup.string().matches(RegExp(/^(0|[+91]{3})?[1-9][0-9]{9}$/), "Invalid Mobile Number").required('Mobile Number is Required'),
    email: Yup.string().email('Invalid email').required('Email is Required'),
    address: Yup.string().required('Address is Required'),
    dob: Yup.date().required("date of birth is required"),
    department: Yup.string().required('Department is required'),
    position: Yup.string().required('Position is Required'),
    status: Yup.string().required('Satatus is required'),
    joinDate: Yup.date().required('Joining Data is required'),
    formerDate: Yup.date(),
    salary: Yup.string().required().max(6, 'Maximum 6 Digits').matches(RegExp(/^(0[+91]{3})?[1-9][0-9]{2,6}$/), 'Invalid Salary'),
    image: Yup.string().notRequired()
  });

  const initValue = {
    title: '',
    name: '',
    father: '',
    email: '',
    dob: null,
    mobileNo: '',
    address: '',
    status: "",
    position: "",
    department: "",
    joinDate: null,
    formerDate: '',
    salary: "",
    image: "",
  }

  const { handleChange, handleSubmit, handleBlur, handleReset, values, setValues, touched, errors } = useFormik({
    initialValues: initValue,
    validationSchema: validationSchema,
    onSubmit: values => {
      const value = values;
      console.log(value);
      const requestData = new FormData();
      const tdata = Object.keys(value);
      tdata.forEach(val => {
        if(val === 'image' && files){
          return requestData.append(val, files?.file)
        }
        requestData.append(val, value[val]);
      });
      sendRequest(requestData);
    }
  });

  console.log(values);

  console.log('tuched',touched);
  console.log('Errore',errors);
  return (
    <div>
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>Add Employee Profile</h4>
              <p className={classes.cardCategoryWhite}>Complete profile</p>
            </CardHeader>
            <CardBody>


              <GridContainer>
                <GridItem xs={12} sm={12} md={3}>
                  <NormalSelect
                    labelText="Name Title"
                    id='title'
                    data={['Mr', 'Mrs', 'Miss']}
                    helperText={touched.title && errors.title}
                    error={Boolean(errors.title && touched.title)}

                    inputProps={{
                      name: 'title',
                      label: 'Name Title',
                      value: values.title,
                      onChange: handleChange
                    }} />
                </GridItem>
                <GridItem xs={12} sm={12} md={8}>
                  <CustomInput
                    labelText="Employee Name"
                    id="name"
                    helperText={touched.name && errors.name}
                    error={Boolean(errors.name && touched.name)}
                    inputProps={{
                      name: 'name',
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.name
                    }}

                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>

                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="Father Name"
                    id="father"
                    helperText={touched.father && errors.father}
                    error={Boolean(errors.father && touched.father)}
                    inputProps={{
                      name: 'father',
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.father,
                      // error={ Boolean(errors.email && touched.email)}

                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="Email Address"
                    id="email"
                    helperText={touched.email && errors.email}
                    error={Boolean(errors.email && touched.email)}
                    inputProps={{
                      name: 'email',
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.email,
                      // error={ Boolean(errors.email && touched.email)}

                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
              </GridContainer>

              <GridContainer>
                <GridItem xs={12} sm={12}>
                  <CustomInput
                    labelText="Address"
                    id="address"
                    helperText={touched.address && errors.address}
                    error={Boolean(errors.address && touched.address)}
                    inputProps={{
                      name: "address",
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.address
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>

              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <DatePicker
                    helperText={errors.dob}
                    error={Boolean(errors.dob && touched.dob)}
                    label='Date of Birth' onChange={(val) => setValues({
                      ...values,
                      dob: val
                    })} value={values.dob}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="Mobile Number"
                    id="mobileNo"
                    helperText={touched.mobileNo && errors.mobileNo}
                    error={Boolean(errors.mobileNo && touched.mobileNo)}
                    inputProps={{
                      name: "mobileNo",
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.mobileNo
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>

              </GridContainer>

              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>

                  <NormalSelect
                    labelText="Department"
                    data={departmentList.map(ele => ele.department)}
                    id='department'
                    helperText={touched.department && errors.department}
                    error={Boolean(errors.department && touched.department)}
                    inputProps={{
                      name: 'department',
                      label: 'department',
                      value: values.department,
                      onChange: (event) => {
                        const val = event.target.value
                        setValues({
                          ...values,
                          department: val
                        })
                        const find = departmentList.find(ele => ele.department === val);
                        setPositionList(find.list)
                      }
                    }} />


                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <NormalSelect
                    labelText="Position"
                    id='position'
                    data={positionList}
                    helperText={touched.position && errors.position}
                    error={Boolean(errors.position && touched.position)}
                    inputProps={{
                      name: 'position',
                      label: 'Position',
                      value: values.position,
                      onChange: (e) => {
                        const val = e.target.value
                        setValues({
                          ...values,
                          position: val,
                          ...(val === 'active' && { formerDate: '' })
                        })

                      }
                    }} />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <DatePicker
                    helperText={touched.joinDate && errors.joinDate}
                    error={Boolean(errors.joinDate && touched.joinDate)}
                    label='Joining Date' onChange={(val) => setValues({
                      ...values,
                      joinDate: val
                    })}
                    value={values.joinDate}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <NormalSelect
                    labelText="Status"
                    id='status'
                    data={['active', 'former']}
                    helperText={touched.status && errors.status}
                    error={Boolean(errors.status && touched.status)}
                    inputProps={{
                      name: 'status',
                      label: 'Position',
                      value: values.status,
                      onChange: handleChange
                    }} />
                </GridItem>

                {values.status === 'former' && <GridItem xs={12} sm={12} md={6}>
                  <DatePicker
                    helperText={touched.formerDate && errors.formerDate}
                    error={Boolean(errors.formerDate && touched.formerDate)}
                    label='former Date' onChange={(val) => setValues({
                      ...values,
                      formerDate: val
                    })}
                    value={values.formerDate}
                  />

                </GridItem>
                }
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="Salary"
                    id="salary"
                    helperText={touched.salary && errors.salary}
                    error={Boolean(errors.salary && touched.salary)}
                    inputProps={{
                      name: "salary",
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.salary
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={3}>
                  
                  {/* <img src={files?.source || ''} alt="preview" /> */}
                  <Button color='primary' round onClick={() => {
                    selectFiles({ accept: "image/*" }, ({ name, size, source, file }) => {
                      console.log("Files Selected", { name, size, source, file });
                    })
                  }
                  } >
                    Image
                  </Button>
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                <Avatar
                    alt="Remy Sharp"
                    src={files?.source || ''}
                    sx={{ width: '100%', height: '100%' }}
                  />
                </GridItem>

              </GridContainer>
            </CardBody>
            <CardFooter>
              <Button onClick={()=>{
                  setValues({
                    ...values,
                    ...(values.dob ?? {dob: ''}),
                    ...(values.joinDate ?? {joinDate: ''})
                })
                handleSubmit()
                }} color="primary">Insert Profile</Button>
            </CardFooter>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
}
